.carousel {
  z-index: 5;
}

.img-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.thumb {
  margin-top: 30px;
}

.thumb img {
  width: 700px;
  border-right: 1px solid white;
  border-radius: 22px;
  -webkit-box-shadow: -4px 7px 6px 2px #000000;
  box-shadow: -4px 7px 6px 2px #000000;
}

.label {
  text-align: center;
  margin: auto;
  margin-top: 20px;
  padding: 0;
  border: 1px solid black;
  border-radius: 22px;
  background-color: #edf2fb;
  -webkit-box-shadow: -7px 7px 3px 4px #000000;
  box-shadow: -7px 7px 3px 4px #000000;
  width: 50%;
  font-family: "Montserrat", sans-serif;
  color: #000000;
}

.label h1 {
  padding-top: 5px;
  color: black;
}

.label p {
  text-align: justify;
  font-size: 1vmax;
  padding: 5px;
}

.active-right {
  animation: right-direction 0.2s forwards;
  transform: translateX(5000px);
  opacity: 0;
}

.active-right-2 {
  animation: right-direction-2 0.2s forwards;
  transform: translateX(5000px);
  opacity: 0;
}

.active-left {
  animation: left-direction 0.2s forwards;
  transform: translateX(-5000px);
  opacity: 0;
}

.active-left-2 {
  animation: left-direction-2 0.2s forwards;
  transform: translateX(-5000px);
  opacity: 0;
}

.right-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

.right-button img {
  background-color: transparent;
}

.left-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: 0.2s;
}

.left-button img {
  background-color: transparent;
  transform: rotate(-180deg);
}

.right-button:hover,
.left-button:hover {
  transform: scale(1.1);
}

@keyframes right-direction {
  from {
    opacity: 0;
    transform: translateX(5000px);
  }

  30% {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes right-direction-2 {
  from {
    opacity: 0;
    transform: translateX(5000px);
  }

  30% {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes left-direction {
  from {
    opacity: 0;
    transform: translateX(-5000px);
  }

  30% {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes left-direction-2 {
  from {
    opacity: 0;
    transform: translateX(-5000px);
  }

  30% {
    opacity: 0;
  }

  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

/* Responsividade */

@media screen and (max-width: 650px) {
  .img-container button {
    position: absolute;
  }

  .thumb {
    margin-top: 1px;
  }

  .left-button {
    position: absolute;
    margin-right: 90%;
    z-index: 5;
  }

  .right-button {
    position: absolute;
    margin-left: 90%;
    z-index: 5;
  }

  .left-button img {
    width: 27.3px;
  }

  .right-button img {
    width: 30px;
  }

  .thumb {
    margin-top: 0;
  }
  .thumb img {
    max-width: 350px;
  }

  .label {
    width: 95%;
    /*  padding-left: 10px;
    padding-right: 10px; */
  }

  .label h1 {
    width: 100%;
    padding: 0;
    font-size: 4.1vmax;
  }

  .label p {
    padding: 5px;
    font-size: 2.5vmax;
  }
}
